.slider {
  text-align: center;

  .indent-clear();
  padding-top: 1px;

  list-style: none;

  background-color: @color-white;
}

.slider__button {
  position: relative;

  display: block;
  width: 10px;
  height: 10px;

  border: none;
  outline: none;
  background-color: transparent;

  cursor: pointer;

  &::after {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 6px;
    height: 6px;

    background-color: rgba(40, 54, 69, 0.3);
    border-radius: 50%;
    cursor: pointer;
  }

  &:hover::after {
    background-color: rgba(40, 54, 69, 0.6);
  }

  &:active::after {
    background-color: rgba(40, 54, 69, 0.1);
  }
}

.slider__button--active {
  height: 15px;
}

.slider__button--active::after {
  background: transparent;
}

.slider__button--active:hover::after,
.slider__button--active:active::after {
  background: transparent;
}

.slider__button--active::before {
  content: "";

  position: absolute;
  top: 0;
  left: 0;

  width: 12px;
  height: 12px;

  border: 3px solid @color-blue;
  border-radius: 50%;
  z-index: 1;
}

.slider__btn {
  display: none;
}

.slider__feedback-info {
  display: none;
}

.slider__feedback-list {
  list-style: none;

  .indent-clear();
  margin-bottom: 35px;
}

.slider__list--left-align {
  position: relative;
  list-style: none;

  .indent-clear();
  margin-bottom: 40px;
}

.slider__list--left-align::after {
  display: block;
  position: absolute;
  content: "";

  height: 300px;
  bottom: 0;
  left: 20px;

  border-left: 2px solid @color-gray;
  background-color: @color-white;
}

.slider__list--left-align::before {
  display: block;
  position: absolute;
  content: "";
  height: 300px;
  bottom: 0;
  right: 20px;

  border-left: 2px solid @color-gray;
}

.slider__item {
  position: relative;

  border-bottom: 2px solid @color-gray;
  text-align: left;

  padding-top: 14px;
  padding-bottom: 14px;
}

.slider__item-svg {
  width: 30px;
  height: 30px;

  position: absolute;
  top: 16px;
  left: -22px;

  fill: @color-green;
}

.icon-no-svg {
  fill: @color-green;
}

.slider__title {
  margin: 0;
  padding: 0;
  padding-left: 44px;

  color: @color-blue;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  text-transform: uppercase;
}

.slider__title svg {
  position: absolute;
  top: 16px;
  right: 41px;

  width: 30px;
  height: 30px;
}

.slider__toggles-list {
  display: flex;
  width: 75px;
  height: 10px;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  list-style: none;

  margin: 0 auto;
  padding: 0;
}

.slider__quotes {
  display: none;
}

.slider__feedback-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;

  margin-bottom: 12px;
}

.slider__title--normal-font {
  color: @color-blue;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
}

.slider__feedback-description {
  display: block;
  width: 280px;

  padding: 0;
  margin: 0;

  color: @color-blue;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
}

@media (min-width: @tablet-width) {
  .slider__feedback-info {
    display: block;

    color: @color-blue;
    font-size: 14px;
    font-weight: 400;
  }

  .slider__button::after {
    top: 0;
    left: 0;
  }

  .slider__feedback-list {
    display: flex;

    margin: 0 auto;
    margin-bottom: 36px;
  }

  .slider__feedback-item {
    display: flex;
    flex: 1;
    flex-direction: column-reverse;

    color: @color-blue;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
  }

  .slider__feedback-description {
    width: 100%;

    margin-bottom: 35px;

    font-size: 18px;
  }

  .slider__feedback-title {
    font-size: 20px;

    margin: 0 auto;
    margin-bottom: 2px;
  }

  .slider__toggles-list {
    margin-bottom: 23px;
  }
}

@media (min-width: @desktop-width) {
  .slider__feedback-info {
    display: block;

    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
  }

  .slider__quotes {
    display: block;

    margin: 0 auto;
    margin-bottom: 50px;

    fill: @color-gray-dark;
  }

  .slider__feedback-list {
    width: 700px;
  }

  .slider__feedback-title {
    font-size: 24px;
  }

  .slider__btn {
    display: block;

    position: absolute;
    top: 142px;

    background: none;
    border: none;

    cursor: pointer;
  }

  .slider__btn svg {
    fill: rgba(40, 54, 69, 0.3);

    &:hover {
      fill: rgba(40, 54, 69, 0.6);
    }

    &:active {
      fill: rgba(40, 54, 69, 0.1);
    }
  }

  .slider__btn--left {
    left: -6px;
  }

  .slider__btn--right {
    right: -6px;
  }

  .slider__toggles-list {
    display: none;
    margin-bottom: 5px;
  }
}
